import React from 'react';
import { useState, useEffect } from 'react';
import { Title, Textbox, Tasks } from './components';
import './App.css';
import TaskService from './services/taskService';


const App = () => {
  const [ tasks, setTasks ] = useState([])
  useEffect(() => {
    TaskService
      .getAll()
      .then(response => {
        setTasks(response.data)
      })
  }, [])

  return (
    <>
      <Title title="Type your tasks below..." />
      <Textbox tasks={tasks} setTasks={setTasks} />

      <Title title="TASKS" />
      <Tasks tasks={tasks} setTasks={setTasks} />

      <Title title="Completed Tasks" />
      <Tasks tasks={tasks} completed={true} setTasks={setTasks} />
    </>
  )
}

export default App;
