import React from 'react';
import './tasks.css'
import TaskService from '../../services/taskService'


const Task = ({ task, tasks, setTasks }) => {
  const handleTaskButton = () => {
    const updatedTask = {
      ...task,
      done: !task.done
    }

    TaskService
      .update(task.id, updatedTask)
      .then(returnedTask => {
        console.log(returnedTask)
        setTasks(tasks.map(currTask => currTask.id === task.id ? returnedTask.data : currTask))
      })
  }

  const handleTaskDeleteButton = () => {
    TaskService
      .deleteTask(task.id)
    
    setTasks(tasks.filter(currTask => currTask.id !== task.id))
  }

  return (
    <>
      <p>
        <button className="taskButton" onClick={handleTaskButton}>{task.done ? 'âœ…' : 'â¬œ'}</button>
        {
          task.done ? <del>{task.taskName}</del> : `${task.taskName}`
        }
        <button className="taskButton" onClick={handleTaskDeleteButton}>âŒ</button>
      </p>
    </>
  )
}

const Tasks = ({ tasks, setTasks, completed }) => {
  if (completed === undefined)
    completed = false
  
  const tasksDisplay = tasks.filter(task => completed === task.done)
                        .map(task => {
                          return <Task key={task.id} task={task} tasks={tasks} setTasks={setTasks} />
                        })

  let taskEmptyMessage = ''

  if (tasks.length === 0) {
    taskEmptyMessage = <p className="taskSubText"><span>Add some tasks to be productive!</span></p>
  } else if (tasks[0].done) {
    taskEmptyMessage = <p className="taskSubText">ğŸ‘ <span> Nice job. You're done for the day</span></p>
  } else {
    taskEmptyMessage = <p className="taskSubText">ğŸ˜­ <span> No tasks completed</span></p>
  }
  return (
    <div className="tasks">
      { tasksDisplay.length > 0 ? tasksDisplay : taskEmptyMessage}
    </div>
  )
}


export default Tasks;